import React from 'react';

const SearchBox = ({ search, onSearchChange }) => (
  <div className='tc'>
    <input
      className='pa3 ba b--green bg-lightest-blue'
      aria-label='Search...'
      id='search'
      placeholder='Search...'
      value={search}
      onChange={onSearchChange}
    />
  </div>
);

export default SearchBox;



